
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i");
@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700,700i");
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;display:-ms-flexbox;display:flex;min-height:100vh;-ms-flex-direction:column;flex-direction:column
}
body{font-family:'Raleway', sans-serif
}
h1,h2,h3,h4,h5,h6,small{font-family:'Open Sans', sans-serif;font-weight:400
}
p{font-family:'Raleway', sans-serif;font-weight:400
}
b{font-weight:700
}
.page-error h4{font-family:'Raleway', sans-serif;font-weight:400
}
.search input{font-family:'Open Sans', sans-serif;font-weight:400
}
.app-details pre.description{font-family:'Raleway', sans-serif;font-weight:400
}
.app-details table tr td{font-family:'Open Sans', sans-serif
}
.app-details table tr td:last-child{font-family:'Raleway', sans-serif
}
.app-details a.app-button div{font-family:'Open Sans', sans-serif
}
.navbar-dark.bg-inverse.translucent .navbar-brand small{font-weight:400
}
body{background-color:#f4f3f4;display:-ms-flexbox;display:flex;min-height:100vh;-ms-flex-direction:column;flex-direction:column
}
body .flex-content{-ms-flex:1;flex:1
}
.pull-right{float:right
}
.pebble{color:#ff4700 !important;display:inline
}
.tag-pebble{background-color:#ff4700
}
.btn-outline-pebble{margin-left:10px;color:#ff4700;border-color:#ff4700;background-image:none;background-color:transparent
}
.btn-outline-pebble:hover{background-color:#ff4700;color:#333
}
.card.subsection,.card.subsection-inverse{max-width:720px;margin-left:auto;margin-right:auto;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);border-radius:0;margin-bottom:40px
}
.card.subsection-inverse{background-color:#333;border-color:#333
}
.navbar-dark.bg-inverse.translucent{background-color:rgba(55,58,60,0.95) !important
}
.navbar-dark.bg-inverse.translucent .navbar-container{min-height:42px
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar-brand{float:none;margin:0;position:relative;top:5px
}
@media screen and (max-width: 370px){
.navbar-dark.bg-inverse.translucent .navbar-container .navbar-brand{margin-left:-10px;top:9px;font-size:1rem
}
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items{position:absolute;top:8px
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items.right{right:16px
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items.left{left:16px
}
@media screen and (max-width: 576px){
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items.left{position:static
}
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items.left a.back{font-size:27px;color:#a5a6a7
}
@media screen and (max-width: 576px){
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items.left a.back{margin-right:20px
}
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items .navbar-toggler,.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items .navbar-toggler:focus{border:0;border-radius:0;outline:none
}
.navbar-dark.bg-inverse.translucent .navbar-container .navbar__items>*{display:inline-block;vertical-align:middle
}
@supports ((-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px))) or (-webkit-backdrop-filter: blur(10px)){
.navbar-dark.bg-inverse.translucent{background-color:rgba(55,58,60,0.88) !important;backdrop-filter:blur(10px);-webkit-backdrop-filter:blur(10px)
}
}
#categorySelector{width:100vw;margin-left:-16px;margin-right:-16px
}
#categorySelector .btn-group .btn-outline-secondary{border-color:rgba(204,204,204,0.5);color:rgba(204,204,204,0.5)
}
#categorySelector .btn-group .btn-outline-secondary.active,#categorySelector .btn-group .btn-outline-secondary:active,#categorySelector .btn-group .btn-outline-secondary:hover{color:#fff;background-color:rgba(204,204,204,0.5);border-color:rgba(204,204,204,0.5)
}
#categorySelector .btn-group .btn-outline-secondary:focus,#categorySelector .btn-group .btn-outline-secondary.focus{border-color:rgba(204,204,204,0.5)
}
header{padding-top:58px;background:linear-gradient(to bottom, #373a3c 0%, #373a3c 65%, rgba(55,58,60,0) 65%, rgba(55,58,60,0) 100%)
}
header .carousel,header .app-banner,header .title-card{max-width:720px;max-height:320px;margin-left:auto;margin-right:auto;box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);background-color:#000
}
header .app-banner img{width:100%;max-width:720px
}
header .title-card{max-height:auto;width:90vw;padding:20px;background-color:#fff
}
header .title-card h3{margin:0
}
header .search{padding:0
}
header .search input{width:100%;height:70px;font-size:1.75rem;padding:20px;border:0;background:none;color:#373a3c
}
header .search input:focus{outline:none
}
.apps{margin-top:40px
}
.apps section{margin-bottom:40px;max-width:850px;margin-left:auto;margin-right:auto
}
.apps section:last-child{margin-bottom:0
}
.apps section .header{height:30px;margin-bottom:10px
}
.apps section .header h6{float:left;display:inline;margin-right:10px;margin-bottom:0
}
.apps section .header a{color:#696969;float:right
}
.apps section .header a:hover,.apps section .header a:focus{text-decoration:none;outline:none
}
.apps section .header-tool{margin-bottom:40px
}
.apps section .card-columns{max-width:800px;margin-left:auto;margin-right:auto;-moz-column-count:3;column-count:3;-moz-column-gap:1.25rem;column-gap:1.25rem
}
@media screen and (min-width: 992px){
.apps section .card-columns{-moz-column-count:4;column-count:4
}
}
@media screen and (max-width: 576px){
.apps section .card-columns{-moz-column-gap:0;column-gap:0
}
}
.apps section .card-columns a{color:#373a3c;text-decoration:none
}
.apps section .card-columns a:hover,.apps section .card-columns a:focus{color:#373a3c;outline:none;text-decoration:none
}
.apps section .card-columns .card{max-width:170px;margin:5px
}
@media screen and (max-width: 576px){
.apps section .card-columns .card{max-width:32vw
}
.apps section .card-columns .card .card-block{padding-left:0.2rem;padding-right:0.2rem
}
.apps section .card-columns .card .card-title{font-size:16px
}
}
.apps section .card-columns .card img{width:100%;height:auto
}
ul.pagination .page-item.active .page-link{background:#ff4700;border-color:#ff4700;color:#fff
}
ul.pagination .page-item.disabled .page-link{color:#818a91
}
ul.pagination .page-item .page-link{color:#ff4700
}
ul.pagination .page-item .page-link:hover,ul.pagination .page-item .page-link:focus{text-decoration:none;outline:none
}



